import "./style";
import {
  Container,
  FirstArticle,
  Img,
  LastArticle,
  SecondArticle,
} from "./style";
import diamondIcon from "../../assets/images/diamong-icon.svg";
import worldIcon from "../../assets/images/world-icon.svg";
import eyeIcon from "../../assets/images/eye-icon.svg";

export default function Mission() {
  return (
    <Container>
      <FirstArticle>
        <Img src={worldIcon} />
        <h1>Misso</h1>
        <p>
          Trabalhar utilizando tcnicas especficas para cada tipo de obra,
          adequando as solues para a necessidade de cada cliente, com o
          objetivo de construir relaes transparentes, prestando servios de
          qualidade e garantindo a satisfao do parceiro.
        </p>
      </FirstArticle>
      <FirstArticle>
        <Img src={eyeIcon} />
        <h1>Viso</h1>
        <p>
          Ser referncia no mercado da construo civil, utilizando tcnicas
          inovadoras de forma sustentvel, minimizando custos e desperdcios.
        </p>
      </FirstArticle>
      <LastArticle>
        <Img src={diamondIcon} />
        <h1>Valores</h1>
        <ol>
          <li> Integridade</li>
          <li> tica e transparencia </li>
          <li> Comprometimento e Responsabilidade</li>
          <li> Atitude e liderana </li>
          <li> Qualidade e Segurana </li>
          <li> Sustentabilidade</li>
          <li> Meritocracia </li>
          <li> Desenvolvimento e Inovao</li>
        </ol>
      </LastArticle>
    </Container>
  );
}
