import {
  Article,
  ArticleText,
  ArticleTitle,
  Container,
  Img,
  Subtitle,
  TextContainer,
  Title,
} from "./style";
import engenheiro from "../../assets/images/logo-cFundo.jpeg";
import aspas from "../../assets/images/aspas-branca.png";

export default function AboutUs() {
  return (
    <Container id="quem-somos">
      <Title>
        Bem-vindo  <p>Paktor</p>
      </Title>
      <Subtitle>
        "A obra do seu jeito, com a qualidade que voc merece."
      </Subtitle>
      <Article>
        <Img src={engenheiro} alt="engineer" />
        <TextContainer>
          <ArticleTitle>Quem somos ns</ArticleTitle>
          <ArticleText>
            &nbsp;&nbsp;&nbsp;&nbsp; A Paktor foi fundada no bairro de Aldeia da
            Serra, pelos engenheiros e scios Fernando Padilha e Gabriel Modena.
            Localizada, atualmente, no bairro Bethaville em Barueri.
            <p /> &nbsp;&nbsp;&nbsp;&nbsp; Atua na rea de engenharia civil, com
            profissionais habilitados em todos os quesitos de construes.
            Oferecem desde os projetos de arquitetura e engenharia,
            gerenciamento de funcionrios, at gesto financeira e dos servios
            executados na obra. <p />
            &nbsp;&nbsp;&nbsp;&nbsp;Executam reformas em casas, apartamentos,
            salas comercial, como tambm constroem empreendimentos desde a
            fundao at o acabamento final. Possuem forte vis sustentvel -
            energia solar e gua de reso-, como principal diferencial em seus
            projetos, agregam valores da vivncia em todas as etapas da obra,
            com conhecimento tcnico necessrio para elaborao dos mesmos, para
            oferecer ao cliente a melhor experincia possvel,{" "}
            <b> sem dor de cabea </b>.
          </ArticleText>
        </TextContainer>
      </Article>
    </Container>
  );
}
